}

@-webkit-keyframes dl {
    0% {
    top: 0em;
    }
    40% {
    top: 0em;
    }
    43% {
    top: -0.9em;
    }
    46% {
    top: 0em;
    }
    48% {
    top: -0.4em;
    }
    50% {
    top: 0em;
    }
    100% {
    top: 0em;
    }
}

