.hero-container{ 
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 75vw;
  height: 75vh;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  box-shadow: 0 20px 50px rgb(23, 32, 90);
  overflow: hidden;
  border: 2px solid #00CCCC;
}

.hero-container:before{
  content: '';
  position:absolute;
  top:0;
  left:-100%;
  width:100%;
  height:100%;
  background: rgba(255,255,255,0.1);
  transition:0.5s;
  pointer-events: none;
  }

.hero-container:hover:before{
  left:-50%;
  -webkit-transform: skewX(-5deg);
          transform: skewX(-5deg);
}

.hero-box{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 30vw;
  height: 50vh;
  padding-bottom: 8%;
  background-color: white;
  border: 2px solid #f0a591;
  opacity: 0.6;
}

.hero-container span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
}

.hero-container span:nth-child(1) {
  -webkit-transform:rotate(0deg);
          transform:rotate(0deg);
}

.hero-container span:nth-child(2) {
  -webkit-transform:rotate(90deg);
          transform:rotate(90deg);
}

.hero-container span:nth-child(3) {
  -webkit-transform:rotate(180deg);
          transform:rotate(180deg);
}

.hero-container span:nth-child(4) {
  -webkit-transform:rotate(270deg);
          transform:rotate(270deg);
}

.hero-container span:before {
  content: '';
  position: absolute;
  width:100%;
  height: 2px;
  background: #33FFCC;
  -webkit-animation: animate 4s linear infinite;
          animation: animate 4s linear infinite;
}

@-webkit-keyframes animate {

  0% {
  -webkit-transform:scaleX(0);
          transform:scaleX(0);
  -webkit-transform-origin: left;
          transform-origin: left;
  }
  
  50% {
  -webkit-transform:scaleX(1);
          transform:scaleX(1);
  -webkit-transform-origin: left;
          transform-origin: left;
  }

  50.1% {
  -webkit-transform:scaleX(1);
          transform:scaleX(1);
  -webkit-transform-origin: right;
          transform-origin: right;  
  }
  
  100% {
  -webkit-transform:scaleX(0);
          transform:scaleX(0);
  -webkit-transform-origin: right;
          transform-origin: right;
  }
  
}

@keyframes animate {

  0% {
  -webkit-transform:scaleX(0);
          transform:scaleX(0);
  -webkit-transform-origin: left;
          transform-origin: left;
  }
  
  50% {
  -webkit-transform:scaleX(1);
          transform:scaleX(1);
  -webkit-transform-origin: left;
          transform-origin: left;
  }

  50.1% {
  -webkit-transform:scaleX(1);
          transform:scaleX(1);
  -webkit-transform-origin: right;
          transform-origin: right;  
  }
  
  100% {
  -webkit-transform:scaleX(0);
          transform:scaleX(0);
  -webkit-transform-origin: right;
          transform-origin: right;
  }
  
} 
